@use '../theme/colors.scss';

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 3vh;
  background-color: colors.$cards-background;

  .links {
    * {
      display: inline-block;
      margin: 0.5rem;
      margin-left: 1rem;
      text-decoration: none;
      color: colors.$texts;
    }
  }

  label {
    color: colors.$texts;
  }

  input {
    padding: 0.5rem;
    border-radius: 10px;
    background-color: colors.$cards-background;
    outline: none;
    color: colors.$texts;
  }
}

@media screen and (max-width: 634px) {
  .navbar {
    display: block;

    .links {
      * {
        display: block;
        margin-left: 0;
      }
    }
  }
}
